<template>
	<view style="height: 90vh;">
		<view class="title-wrapper">
			<text class="title">创建群聊</text>
		</view>
		<view class="notice-wrapper">
			<text class="notice">请勿在群聊中传播违法信息</text>
		</view>
		
		<view class="form-wrapper">
			<text class="form-text">群名称：</text>
			<input type="text" placeholder="填写群名称(4~32个字)" maxlength="32"/>
		</view>
		<view style="height: 5%;"></view>
		<view class="form-wrapper">
			<text class="form-text">群描述：</text>
			<textarea placeholder="填写群描述(5~50个字)" maxlength="150" style="height: 100%;"></textarea>
		</view>
		
		<view style="height: 5%;"></view>
		<view class="form-wrapper">
			<text class="form-text">群标签：</text>
			<view class="tag-wrapper">
				<uni-tag v-for="tagName in tags" class="group-tag" size="small" :text="tagName" :type="randomColor()"></uni-tag>
				<uni-tag class="group-tag" text="+" inverted="true" size="small" type="primary"></uni-tag>
			</view>
		</view>
		
		<button class="create-button" type="primary">立即创建</button>
	</view>
</template>
<script>
	import UniTag from '../../components/uni-tag/uni-tag.vue'
	export default {
		components:{UniTag},
		data() {
			return {
				tags: [
					'java', 'python', 'javascript', 'Vue', 'MySQL'
				]
			}
		},
		methods: {
			randomColor(){
				let colors = ['primary', 'warning', 'error', 'success']
				return colors[this.randomNum(0, 3)]
			},
			randomNum(minNum,maxNum){ 
			    switch(arguments.length){ 
			        case 1: 
			            return parseInt(Math.random()*minNum+1,10); 
			        break; 
			        case 2: 
			            return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10); 
			        break; 
			            default: 
			                return 0; 
			            break; 
			    } 
			} 
		}
	}
</script>
<style scoped src="./addGroup.css"></style>
